<template>
    <v-container fluid fill-height style="padding:0;" id="mouse" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Explain">解释</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <p class=".body-1">1. 通过<code>fillRect</code>代替<code>clearRect</code>可以实现尾影效果</p>
                                    <p class=".body-1">2. <code>getBoundingClientRect()</code>可以获取canvas的距离屏幕的边距</p>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Result1">效果（移动鼠标查看效果）</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <canvas id="canvas" width="800px" height="500px"></canvas>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Code1">代码</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                    <v-tab key="html" ripple>HTML</v-tab>
                                    <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                    <v-tab-item key="html">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">
                                                    {{html}}
                                                </pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                    <v-tab-item key="js">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">{{js}}</pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                </v-tabs>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import { setInterval } from 'timers';
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-10-5",
            title: "在canvas中获取鼠标位置",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            <canvas 
                id="canvas" 
                width="800px" 
                height="500px"
            >
            </canvas>
            `,
            js: `
            const canvas = document.getElementById('canvas');
            const ctx = canvas.getContext('2d');
            
            canvas.addEventListener('mousemove', function(e){
                // 获取鼠标位置
                const cRect = canvas.getBoundingClientRect();              
                const mouseX = Math.round(e.clientX - cRect.left);        
                const mouseY = Math.round(e.clientY - cRect.top);
                movePentagram(mouseX, mouseY, function(){
                    ctx.fillStyle = 'green';
                    ctx.font = "25px cursive"
                    ctx.fillText("X: "+mouseX+", Y: "+mouseY, 10, 20);
                });
            })

            // 让五角星垂直下落，逐渐淡出视野
            function movePentagram(x, y, fallback) {
                let animate = null;
                let step = 0;
                let end = 100;
                window.requestAnimationFrame(draw);
                function draw() {
                    step+=1;
                    ctx.save();
                    ctx.fillStyle = 'rgba(255, 255, 255, 0.3)';
                    ctx.fillRect(0, 0, canvas.width, canvas.height);
                    ctx.fillStyle = \`rgba(75,241,104,\${(100-step)/100})\`;
                    ctx.beginPath();

                    if (step < end) {
                        let random = (2*Math.random() - 1);
                        generatePentagram(x, y+step, 10, 7);
                        fallback();
                        ctx.restore();
                        animate = window.requestAnimationFrame(draw);
                    } else {
                        window.cancelAnimationFrame(animate);
                    }
                }
            }

            // 生成五角星
            function generatePentagram(x, y, stepx, stepy) {
                ctx.beginPath();
                ctx.moveTo(x, y);
                ctx.lineTo(x+stepx, y+stepy);
                ctx.lineTo(x+stepx*2, y+stepy);
                ctx.lineTo(x+stepx, y+stepy*2);
                ctx.lineTo(x+stepx*1.5, y+stepy*3);
                ctx.lineTo(x, y+stepy*2);
                ctx.lineTo(x-stepx*1.5, y+stepy*3);
                ctx.lineTo(x-stepx, y+stepy*2);
                ctx.lineTo(x-stepx*2, y+stepy);
                ctx.lineTo(x-stepx, y+stepy);
                ctx.lineTo(x, y);
                ctx.fill();
            }
            `
        };
    },
    mounted() {
        const canvas = document.getElementById('canvas');
        const ctx = canvas.getContext('2d');
        
        canvas.addEventListener('mousemove', function(e){
            // 获取鼠标位置
            const cRect = canvas.getBoundingClientRect();              
            const mouseX = Math.round(e.clientX - cRect.left);        
            const mouseY = Math.round(e.clientY - cRect.top);
            movePentagram(mouseX, mouseY, function(){
                ctx.fillStyle = 'green';
                ctx.font = "25px cursive"
                ctx.fillText("X: "+mouseX+", Y: "+mouseY, 10, 20);
            });
        })

        // 让五角星垂直下落，逐渐淡出视野
        function movePentagram(x, y, fallback) {
            let animate = null;
            let step = 0;
            let end = 100;
            window.requestAnimationFrame(draw);
            function draw() {
                step+=1;
                ctx.save();
                ctx.fillStyle = 'rgba(255, 255, 255, 0.3)';
                ctx.fillRect(0, 0, canvas.width, canvas.height);
                ctx.fillStyle = `rgba(75,241,104,${(100-step)/100})`;
                ctx.beginPath();

                if (step < end) {
                    let random = (2*Math.random() - 1);
                    generatePentagram(x, y+step, 10, 7);
                    fallback();
                    ctx.restore();
                    animate = window.requestAnimationFrame(draw);
                } else {
                    window.cancelAnimationFrame(animate);
                }
            }
        }

        // 生成五角星
        function generatePentagram(x, y, stepx, stepy) {
            ctx.beginPath();
            ctx.moveTo(x, y);
            ctx.lineTo(x+stepx, y+stepy);
            ctx.lineTo(x+stepx*2, y+stepy);
            ctx.lineTo(x+stepx, y+stepy*2);
            ctx.lineTo(x+stepx*1.5, y+stepy*3);
            ctx.lineTo(x, y+stepy*2);
            ctx.lineTo(x-stepx*1.5, y+stepy*3);
            ctx.lineTo(x-stepx, y+stepy*2);
            ctx.lineTo(x-stepx*2, y+stepy);
            ctx.lineTo(x-stepx, y+stepy);
            ctx.lineTo(x, y);
            ctx.fill();
        }
    }
};
</script>

<style lang="scss">
#mouse {
    .blogcontent {
        padding-top: 20px;
    }
    .code {
        font-size: 12px;
        background-color: #f9f9f5;
        white-space: pre-wrap;
        word-wrap: break-word;
    }
    canvas {
        border: 1px solid #ccc;
    }
}
</style>

